.mainWrapper {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid rgb(214, 207, 207);
  -webkit-box-shadow: 0px 0px 9px 2px rgba(201, 201, 201, 1);
  -moz-box-shadow: 0px 0px 9px 2px rgba(201, 201, 201, 1);
  box-shadow: 0px 0px 9px 2px rgba(201, 201, 201, 1);

}

.title {
  width: 100%;
  border-bottom: 1px solid rgb(214, 207, 207);
  padding: 10px 0;
  text-align: center;
  -webkit-box-shadow: 0px 1px 5px 0px rgba(201, 201, 201, 1);
  -moz-box-shadow: 0px 1px 5px 0px rgba(201, 201, 201, 1);
  box-shadow: 0px 1px 5px 0px rgba(201, 201, 201, 1);
}